<template>
  <div>
    <el-card class="g_label" v-loading="diaLoading">
      <el-row
        justify="space-between"
        type="flex"
        :gutter="20"
        class="oneRow"
        style="margin-bottom: 6px;"
      >
        <el-col :span="24">
          <el-row class="g_label" v-for="(Uname,index) in UsersNames" :key="index">
            <el-col :span="24">
              <div
                class="div_bas0 bg-purple-dark"
                style="border-radius: 5px; padding: 2px; margin: 0 0 3px 5px; height: 24px;"
              >
                <div
                  class="grid-content bg-purple-left"
                  style="width: 100px; display: inline-block;"
                  align="right"
                >{{Uname}}</div>
                <div
                  v-for="(UAuth,cindex) in UsersAuths[index]"
                  :key="cindex"
                  :class="barClass(cindex)"
                >{{UAuth.authority_description}}</div>
              </div>
            </el-col>
          </el-row>

          <!-- <el-card
            class="g_label"
            v-for="(Uname,key) in UsersNames"
            :body-style="{ padding: '0px' }"
            :key="key"
            style="padding: 10px 0 0 10px; margin:0 0 0 0; height: 22px;"
          >{{Uname}}</el-card>-->
          <!-- <el-tag
            v-for="(Uname,key) in UsersNames"
            :key="key"
            size="small"
            style="padding:0 5px; margin:0 1px;"
          >{{Uname}}</el-tag>-->
        </el-col>
      </el-row>
      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
          stripe
          :cell-style="finalCellStyle"
          row-key="auth_id"
        >
          <el-table-column type="selection" fixed="left" reserve-selection width="55"></el-table-column>
          <el-table-column prop="auth_id" label="序号" align="center" width="100px"></el-table-column>
          <el-table-column prop="authority_name" label="权限名称" align="center"></el-table-column>
          <el-table-column prop="authority_description" label="权限说明" align="center"></el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <el-col :span="3" style="padding: 16px 0 5px 0;">
          <el-button
            type="primary"
            :icon="butIcoSubmit"
            :disabled="butDisabledSubmit"
            @click="saveAuth"
            size="small"
            style="margin-left: 10px;"
          >保存权限</el-button>
        </el-col>
        <el-col :span="4" style="padding: 14px 0 5px 0;" align="left">
          <el-alert
            title="请选择要设置的权限"
            type="warning"
            ref="alert"
            style="padding-left: 5px; width: 180px;"
          ></el-alert>
        </el-col>
        <el-col :span="17">
          <div class="page_div">
            <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              layout="total, prev, pager, next"
              :total="pageTotal"
            ></el-pagination>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      readOnlyFlag: true,
      saveing: false, // 是否正在保存
      // 列表信息
      listdata: [],

      UsersId: [], // 要设置权限的用户ID数组
      UsersNames: [], // 要设置权限的用户名数组
      UsersAuths: [], // 要设置权限的用户现拥有的权限

      diaLoading: false,
      butIcoSubmit: 'el-icon-upload',
      butDisabledSubmit: false,

      selId: [],
      selNames: [],
      currentPage: 1,
      pageSize: 11,
      pageTotal: 0
    }
  },
  computed: {},
  created() {
    if (this.$Storage.localGet('selId')) {
      this.UsersId = this.$Storage.localGet('selId')
      this.UsersNames = this.$Storage.localGet('selNames')
    } else {
      // this.$router.push({ path: '/menmessage' })
      this.$router.push(
        '/mammanmessage',
        () => {},
        e => {
          console.log('输出报错', e)
        }
      )
    }
    this.getAuthList()
    this.getUserAuthAll()
  },
  mounted() {},
  methods: {
    // 获取权限信息
    getAuthList() {
      var apiUrl =
        '/api' +
        '/auth/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      this.$http
        .get(apiUrl)
        .then(res => {
          if (res) {
            // console.log('getAuthList res:', res, res.data)
            if (res.data.code === 0) {
              this.listdata = res.data.data.list
              this.pageTotal = res.data.data.total
              // console.log('getAuthList this.listdata:', this.listdata)
            } else {
              this.urResponseErr('获取权限信息失败：' + res.data.message)
            }
          } else {
            this.urResponseErr('获取权限信息失败！')
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取人员信息失败')
        })
    },

    // 获取用户权限-all
    getUserAuthAll() {
      this.UsersAuths = []
      var myApis = []
      for (let i = 0; i < this.UsersId.length; i++) {
        myApis[i] = this.$http.get(
          '/api' + '/user/find/auth?' + 'userId=' + this.UsersId[i]
        )
      }
      // console.log('getUserAuthAll myApis:', myApis)
      this.$http
        .all(myApis)
        .then(res => {
          if (res) {
            var myAuths = []
            for (let i = 0; i < res.length; i++) {
              // console.log('getUserAuthAll res[' + i + ']:', res[i], res[i].data)
              myAuths[i] = []
              if (res[i].data.code === 0) {
                console.log(
                  'getUserAuthAll res[' + i + '].data.data:',
                  res[i].data.data
                )
                for (let j = 0; j < res[i].data.data.length; j++) {
                  // this.$set(this.UsersAuths[i], j, res[i].data.data[j])
                  myAuths[i][j] = res[i].data.data[j]
                }
                console.log(i, 'getUserAuth this.UsersAuths[i]:', this.UsersAuths[i])
              } else {
                // this.$set(this.UsersAuths[i], 0, '[' + res[i].data.message + ']')
                myAuths[i][0] = '[' + res[i].data.message + ']'
                // this.urResponseErr('获取用户权限失败：' + res[i].data.message)
              }
            }
            this.UsersAuths = myAuths.slice()
          } else {
            this.urResponseErr('获取用户权限失败！')
          }
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取用户权限失败！')
        })
    },

    // 提交用户信息
    saveAuth() {
      this.udShowStyle(true)
      var apiUrl = '/api' + '/user/save/auth'
      var saveAuth = { userId: this.UsersId, auths: this.selId }
      var adTitle = '设置用户权限'
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      const formData = new FormData()
      for (var I in saveAuth) {
        formData.append(I, saveAuth[I])
      }
      this.$http
        .post(apiUrl, formData, config)
        .then(res => {
          if (res) {
            // console.log('apiSaveUser res:', res, res.data)
            if (res.data.code === 0) {
              this.dialogVisible = false
              this.$message({
                message: adTitle + '成功！',
                type: 'success'
              })
              this.getUserAuthAll()
            } else {
              this.urResponseErr(adTitle + '失败：' + res.data.message)
            }
          } else {
            this.urResponseErr(adTitle + '失败！')
          }
          // this.udShowStyle(false, true)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('err:', err)
          this.urResponseErr(adTitle + '失败:' + err)
          // this.udShowStyle(false, true)
        })
      // eslint-disable-next-line no-unreachable
      this.udShowStyle(false, true)
    },

    // 提交开始或结束
    udShowStyle(_start, _noInt) {
      if (_start) {
        this.butIcoSubmit = 'el-icon-loading'
        this.butDisabledSubmit = true
        this.diaLoading = true
      } else {
        if (_noInt) {
        } else {
          this.intSimData()
        }
        this.butIcoSubmit = 'el-icon-upload'
        this.butDisabledSubmit = false
        this.diaLoading = false
      }
    },
    // 提交返回失败的处理：
    urResponseErr(_errmsg) {
      this.udShowStyle(false, true)
      this.$message.error(_errmsg)
    },

    // 批量操作复选框
    handleSelectionChange(val) {
      this.selId = []
      this.selNames = []
      val.forEach((ele, indx) => {
        this.selId.push(ele.auth_id)
        this.selNames.push(ele.authority_name)
      })
      this.$refs.alert.visible = !(this.selId.length > 0)
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getAuthList(this.currentPage)
    },

    // 表单项样式：
    finalCellStyle({ row, column, rowIndex, columnIndex }) {
      // if (row['value'] != row['valueInit']) {
      //   return {
      //     background: '#f7b1b180'
      //   }
      // } else {
      //   return {
      //     background: ''
      //   }
      // }
      return { padding: '7px 0' }
    },

    // 权限小卡样式
    barClass(_index) {
      if (_index % 2 === 0) {
        return 'bar bar-purple-A'
      } else {
        return 'bar bar-purple-B'
      }
    }
  }
}
</script>
<style lang="less">
.bg-purple-dark {
  background: #eeeeee;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-left {
  background: #ffffff;
}
.bar {
  display: inline;
  padding: 2px 4px;
  margin: 0 1px;
  border-radius: 4px;
  border: 1px solid #ddddcd;
}
.bar-purple-A {
  background: #eeeeff;
}
.bar-purple-B {
  background: #ffeeee;
}
.grid-content {
  border-radius: 5px 0 0 5px;
  min-height: 20px;
  margin: 0 3px 0px 0px;
  padding: 4px 8px 0px 8px;
}
</style>
